@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
input {
  all: unset;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

@font-face {
  font-family: "bmf";
  src: url(/cgchat/static/media/font.36832d8a.otf) format("truetype");
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: white;
  font-family: bmf;
  font-size: 14px;
  color: white;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

h1 {
  font-size: 24px;
  color: rgb(99 102 241);
  font-family: bmf;
}
h1:hover {
  font-size: 24px;
  color: #4c47e0;
  font-family: bmf;
}


h3 {
  color: white;
  font-family: bmf;
}

@media (prefers-color-scheme: light) {
  h3{
    color:black;
  font-family: bmf;
  }
}


@media (prefers-color-scheme: dark) {
  body {
    background-color: rgb(28 25 23);
    font-family: bmf;

    font-size: 14px;
    color: white;
  }
}

img {
  content: url(/cgchat/static/media/logob.61de5ee9.png);
}

@media (prefers-color-scheme: light) {
  img {
    content: url(/cgchat/static/media/logoa.213e7b53.png);
  }
}

span {
  color: rgb(99 102 241);
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}
span:hover {
  color: #4c47e0;
}

.nweet-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color:  rgb(99 102 241);
  cursor: pointer;
}

.formBtn:hover {
  background-color: #4c47e0;
}



.cancelBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: rgb(252, 68, 35);
}
.cancelBtn:hover {
  color: white;
  background-color: rgb(255, 47, 10);


}

/* Profile Page */

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

@media (prefers-color-scheme: light) {
  .profileForm {
    border-bottom: 1px solid rgba(0, 0, 0, 0.9);

  }
}

.logOut {
  margin-top: 50px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid rgb(99 102 241);
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}


@media (prefers-color-scheme: light) {
  .factoryInput__input {
  color:black;
  }
}

.factoryInput__arrow {
  position: absolute;
  right : 0;
  background-color: rgb(99 102 241);
  height: 40px;
  width: 40px;
  font-size: 25px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
  box-sizing: border-box;
}

.factoryInput__label {
  color: rgb(99 102 241);
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm__attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
}

.factoryForm__clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* Nweet */

.nweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.nweet {
  margin-bottom: 10px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  word-wrap: break-word;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

@media (prefers-color-scheme: light) {
  .nweet {
    background-color: wheat;
  color:black;
  }
}

.nweet h4 {
  font-size: 14px;
}

.nweet img {
  right: -10px;
  top: 20px;
  position: absolute;
  border-radius: 10%;
  width: 50px;
  height: 50px;
  margin-top: 10px;
}

.nweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.nweet__actions span {
  cursor: pointer;
}

.nweet__actions span:first-child {
  margin-right: 10px;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background-color: #6365f1;
  color: white;
  cursor: pointer;
}
.authBtn:hover {
  background-color: #4c47e0;
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
  box-sizing: content-box;
}

input.password {
  font-family: 'pass', 'Roboto', Helvetica, Arial, sans-serif ;
  font-size: 18px;
  color:black;
  &::-webkit-input-placeholder {
    transform: scale(0.77);
    transform-origin: 0 50%;
  }
  &::-moz-placeholder {
    font-size: 14px;
    opacity: 1;
  }
  &:-ms-input-placeholder {
    font-size: 14px;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
  }
}

@media (prefers-color-scheme: light) {
  .authInput {
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    background-color: rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
    font-size: 12px;
    color: black;
    box-sizing: content-box;
  }
}

.authSubmit {
  text-align: center;
  background-color: rgb(99 102 241);
  color: white;
  margin-top: 10px;
  box-sizing: content-box;
  cursor: pointer;
}

.authSubmit:hover {
  background-color: #4c47e0;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: rgb(99 102 241);
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}
.authSwitch:hover {
  color: #4c47e0;
}

